import pdfBackground from './background.png';
import pdfBackgroundMinified from './background-minified.png';
import scissorsIcon from './scissors.png';
import noorIslamLogo from './noor-islam-filled.svg';
import visionLogo from './vision.svg';
import maarifLogo from './maarif-logo.svg';
// import maarifLogo from './maarif-logo-160.png';

import './App.css';

const theme = createTheme({
	components: {
		MuiInputLabel: {
			styleOverrides: {
				root: {
					left: 'inherit',
					right: '1.75rem',
					transformOrigin: 'right',
				},
			},
		},
		MuiOutlinedInput: {
			styleOverrides: {
				notchedOutline: {
					textAlign: 'right',
				},
			},
		},
	},
	shadows: ['none'],
});

// const CANVAS_FONT_SIZE = 500;
// const CANVAS_FONT_SIZE = 20;
// const TABLE_OFFSET_X = 88;
// const TABLE_OFFSET_Y = 192.1;
// const TABLE_WIDTH = 420;
