// const TABLE_COLUMN_SPACING = TABLE_RIGHT_PADDING;
// const TABLE_ROW_DIVIDER_PADDING = 3;

// const WEB_TABLE_COLUMN_SPACING = 6;
// const MINIFIER_VALUE = 0.28571428571;
const MINIFIER_VALUE = 0.42857142857;
const MINIFIED_PADDING = 20;
const MINIFIED_BOTTOM_PADDING = 10;
// const OFFSET = 2 * MINIFIER;
const OFFSET = 2;

const CANVAS_FONT_SIZE = 20 * OFFSET;
const TABLE_OFFSET_X = 88 * OFFSET;
const TABLE_OFFSET_Y = 212.1 * OFFSET;
// const TABLE_OFFSET_Y = 165.1 * OFFSET;
const TABLE_WIDTH = 420 * OFFSET;
const TABLE_RIGHT_PADDING = 10 * OFFSET;
const TABLE_ROW_SPACING = 15 * OFFSET;
const TABLE_TOP_PADDING = TABLE_ROW_SPACING / 2;
const TABLE_COLUMN_SPACING = TABLE_RIGHT_PADDING;
const TABLE_ROW_DIVIDER_PADDING = 3 * OFFSET;

const WEB_TABLE_COLUMN_SPACING = 6;
// { "committee": 1, "class": "", "area": "1/2", "seat-number": 44001 },

const switches = {
	'seat-number': ' ',
	class: '',
	area: '',
	committee: '',
	national: ' ',
};

function App() {
	const [state, _setState] = useState({
		error: null,
		field: '',
		info: null,
		loading: false,
		anchor: null,
		latency: 0,
	});
	const _state = useRef(state);
	const setState = (data) => {
		_state.current = data;
		_setState(data);
	};

	const [ping, setPing] = useState('.');

	const pdf = new jsPDF();
	const field = useRef(null);
	const canvas = useRef(null);
	const download = useRef(null);
	const saveButton = useRef(null);

	const onBlur = (event) => {
		if (!event.relatedTarget) return;

		if (event.relatedTarget.id == 'main-button') {
			setTimeout(function () {
				event.target.focus();
			}, 20);
		}
	};

	const measureText = (text, font) => {
		let context = canvas.current.getContext('2d');
		let oldFont = context.font;

		context.font = font;
		let measured = context.measureText(text);
		context.font = oldFont;

		return measured.width;
	};

	useEffect(() => {
		setPing(
			setInterval(async () => {
				// let result = await fetch(ping', {
				let start = new Date();
				// let result = await fetch('http://localhost:3001/ping');
				let result = await fetch('/ping');
				let latency = new Date() - start;

				setState({
					..._state.current,
					latency: latency,
				});
			}, 5000)
		);

		return () => {
			clearInterval(ping);
		};
	}, []);

	const handleClick = (event) => {
		setState({
			...state,
			anchor: event.currentTarget,
		});
	};
	const handleClose = () => {
		setState({
			...state,
			anchor: null,
		});
	};

	const isNationalValid = (national) => national.toString().match(/^\d+$/g) != null || national.length == 0;

	const onFieldChange = (event) => {
		if (event.target.value.length > 13 || !isNationalValid(event.target.value)) return;

		setState({
			...state,
			error: null,
			field: event.target.value,
		});
	};

	const onRequestSearch = async (event) => {
		if (state.field == '') {
			setState({
				...state,
				info: {
					'seat-number': ' ',
					class: '',
					area: '',
					committee: '',
					national: ' ',
				},
			});
			return;
		}

		if (!isNationalValid(state.field)) {
			return setState({
				..._state.current,
				info: null,
				error: '  ',
			});
		}

		const national = state.field;

		setState({
			..._state.current,
			loading: true,
			info: null,
			error: null,
		});

		// try {
		// .then((response) => {
		// 	if (response.status >= 400 && response.status < 600) {
		// 	  throw new Error("Bad response from server");
		// 	}
		// 	return response;
		// }).then((returnedResponse) => {
		//    // Your response to manipulate
		//    this.setState({
		// 	 complete: true
		//    });
		// }).catch((error) => {
		//   // Your error is here!
		//   console.log(error)
		// });

		let result = await fetch('/search', {
			// let result = await fetch('http://localhost:3001/search', {
			body: JSON.stringify({
				national: national,
			}),
			headers: {
				'Content-Type': 'application/json',
			},
			method: 'POST',
		})
			.then((response) => {
				if (response.status >= 400 && response.status < 600) {
					setState({
						...state,
						loading: false,
						info: null,
						error: '   ',
					});
					// throw new Error("Bad response from server");
				} else {
					return response;
				}
			})
			.catch((error) => {
				setState({
					...state,
					loading: false,
					info: null,
					error: error.toString(),
				});
			});

		if (result == undefined) return;

		console.log(result.headers);

		let json = await result.json();
		// } catch (error) {
		// 	return setState({
		// 		...state,
		// 		loading: false,
		// 		info: null,
		// 		error: error.toString(),
		// 	});
		// }

		if (json.error)
			return setState({
				..._state.current,
				loading: false,
				info: null,
				error: json.reason,
			});

		delete json.error;

		let keyValues = Object.entries(json);
		keyValues.splice(0, 0, ['national', national]);
		json = Object.fromEntries(keyValues);

		setState({
			..._state.current,
			loading: false,
			error: null,
			info: json,
		});
	};

	const closeInfo = () => {
		setState({
			...state,
			error: null,
			info: null,
		});
	};

	const organizeInfo = () => {
		let texts = [];
		for (let [key, value] of Object.entries(state.info)) {
			texts.push({
				name: switches[key],
				value: value,
			});
		}
		return texts;
	};

	const loadImage = (source) => {
		return new Promise((resolve, reject) => {
			let image = new Image();
			image.onload = function (event) {
				resolve({
					image: image,
					width: this.width,
					height: this.height,
				});
			};
			image.src = source;
		});
	};

	function degreesToRadians(degrees) {
		var pi = Math.PI;
		return degrees * (pi / 180);
	}

	const contextLine = (context, x1, y1, x2, y2, dash = null) => {
		context.beginPath();
		context.setLineDash(dash ? dash : []);
		context.moveTo(x1, y1);
		context.lineTo(x2, y2);
		context.stroke();
	};

	const drawRotatedImage = (ctx, image, x, y, width, height, angle) => {
		let radians = degreesToRadians(angle);
		ctx.translate(x, y);
		ctx.rotate(radians);
		ctx.drawImage(image, -width / 2, -height, width, height);
		ctx.rotate(-radians);
		ctx.translate(-x, -y);
	};

	const getInfoAsBase64 = async (minifier = 1, backgroundImage = null, settings = null) => {
		let {
			canvas_font_size,
			table_row_spacing,
			table_top_padding,
			table_row_divider_padding,
			table_offset_x,
			table_offset_y,
			table_width,
			table_right_padding,
			table_column_spacing,
			minified_padding,
			minified_bottom_padding,
		} = settings || {
			'canvas_font_size': CANVAS_FONT_SIZE,
			'table_offset_x': TABLE_OFFSET_X,
			'table_offset_y': TABLE_OFFSET_Y,
			'table_width': TABLE_WIDTH,
			'table_right_padding': TABLE_RIGHT_PADDING,
			'table_row_spacing': TABLE_ROW_SPACING,
			'table_top_padding': TABLE_TOP_PADDING,
			'table_column_spacing': TABLE_COLUMN_SPACING,
			'table_row_divider_padding': TABLE_ROW_DIVIDER_PADDING,
			'minified_padding': 0,
			'minified_bottom_padding': 0,
		};

		var ctx = canvas.current.getContext('2d');
		let texts = organizeInfo();

		let background = await loadImage(backgroundImage || pdfBackground);

		canvas.current.width = background.width;
		canvas.current.height = background.height;
		// ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
		ctx.fillStyle = 'white';
		ctx.fillRect(0, 0, background.width, background.height);
		ctx.fillStyle = 'black';
		ctx.drawImage(background.image, 0, 0, background.width, background.height);

		// ctx.strokeStyle = '#8F8F8F';
		ctx.strokeStyle = 'black';

		const tableHeight = canvas_font_size + (texts.length - 1) * (canvas_font_size + table_row_spacing) + table_top_padding + table_row_spacing / 2 + table_row_divider_padding;
		contextLine(ctx, table_offset_x, table_offset_y, table_offset_x + table_width, table_offset_y);
		contextLine(ctx, table_offset_x + table_width, table_offset_y, table_offset_x + table_width, table_offset_y + tableHeight);
		ctx.font = `${canvas_font_size}px Arial`;

		let maxWidth = texts.map(({ name, value }) => ctx.measureText(name).width).sort((a, b) => b - a)[0];

		for (let i = 0; i < texts.length; i++) {
			let text = texts[i];
			let { name, value } = text;

			ctx.font = `${canvas_font_size}px Arial`;

			ctx.fillText(
				name,
				table_offset_x + table_width - ctx.measureText(name).width - table_right_padding,
				canvas_font_size + table_offset_y + i * (canvas_font_size + table_row_spacing) + table_top_padding
			);

			let y = canvas_font_size + table_offset_y + i * (canvas_font_size + table_row_spacing) + table_top_padding + table_row_spacing / 2 + table_row_divider_padding;

			contextLine(ctx, table_offset_x, y, table_offset_x + table_width, y);

			ctx.fillText(
				value,
				table_offset_x + table_width - ctx.measureText(value).width - table_right_padding * 2 - maxWidth - table_column_spacing,
				canvas_font_size + table_offset_y + i * (canvas_font_size + table_row_spacing) + table_top_padding
			);
		}

		contextLine(
			ctx,
			table_offset_x + table_width - maxWidth - table_right_padding - table_column_spacing,
			table_offset_y,
			table_offset_x + table_width - maxWidth - table_right_padding - table_column_spacing,
			table_offset_y + tableHeight
		);

		contextLine(ctx, table_offset_x, table_offset_y, table_offset_x, table_offset_y + tableHeight);

		let warning = '      ';
		// ctx.font = `60px custom-arabic`;
		ctx.font = `${60 * minifier}px custom-arabic`;

		// Add scissors and dashed line
		// ctx.fillText(warning, ctx.canvas.width / 2 - ctx.measureText(warning).width / 2, table_offset_y + tableHeight + 30 * 2 + 65);
		ctx.fillText(warning, (ctx.canvas.width * minifier) / 2 - ctx.measureText(warning).width / 2 + minified_padding, table_offset_y + tableHeight + (30 * 2 + 65) * minifier);
		contextLine(ctx, minified_padding, table_offset_y + tableHeight + 30 * 2 * minifier, ctx.canvas.width * minifier + minified_padding, table_offset_y + tableHeight + 30 * 2 * minifier);
		ctx.strokeStyle = '#8F8F8F';

		contextLine(
			ctx,
			0,
			table_offset_y + tableHeight + (30 * 2 + 100) * minifier + minified_padding - minified_bottom_padding,
			ctx.canvas.width,
			table_offset_y + tableHeight + (30 * 2 + 100) * minifier + minified_padding - minified_bottom_padding,
			[10 * minifier, 5 * minifier]
		);
		contextLine(ctx, ctx.canvas.width * minifier + minified_padding * 2, 0, ctx.canvas.width * minifier + minified_padding * 2, ctx.canvas.height, [10 * minifier, 5 * minifier]);

		let scissors = await loadImage(scissorsIcon);
		let size = 32 * minifier;
		if (minifier != 1) {
			// const drawRotatedImage = (ctx, image, x, y, width, height, angle) => {
			// ctx.drawImage(scissors.image, 50 * minifier, table_offset_y + tableHeight + (30 * 2 + 100) * minifier - size / 2, size, size);
			drawRotatedImage(
				ctx,
				scissors.image,
				(ctx.canvas.width + 50) * minifier + minified_padding * 2,
				table_offset_y + tableHeight + (30 * 2 + 100) * minifier - size / 2 + minified_padding - minified_bottom_padding,
				size,
				size,
				180
			);
			drawRotatedImage(
				ctx,
				scissors.image,
				ctx.canvas.width * minifier + size / 2 + minified_padding * 2,
				table_offset_y + tableHeight + (30 * 2 + 166) * minifier - size / 2 + minified_padding - minified_bottom_padding,
				size,
				size,
				270
			);
		} else {
			ctx.drawImage(scissors.image, 50 * minifier, table_offset_y + tableHeight + (30 * 2 + 100) * minifier - size / 2, size, size);
		}
		// if(minifier)

		return ctx.canvas.toDataURL('image/jpeg');
	};

	const saveAsPhoto = async () => {
		let data = await getInfoAsBase64();
		download.current.href = data;
		download.current.click();
	};

	const scaleDimensionsTo = (dimensions, newWidth) => {
		let { width, height } = dimensions;
		let ratio = width / newWidth;
		return {
			width: width / ratio,
			height: height / ratio,
		};
	};

	const saveAsDocument = async (minified = false) => {
		let data = await getInfoAsBase64(
			minified ? MINIFIER_VALUE : 1,
			minified ? pdfBackgroundMinified : pdfBackground,
			minified
				? {
						canvas_font_size: 20 * OFFSET * MINIFIER_VALUE,
						table_offset_x: (88 + MINIFIED_PADDING) * OFFSET * MINIFIER_VALUE,
						table_offset_y: (165.1 + MINIFIED_PADDING) * OFFSET * MINIFIER_VALUE,
						table_width: 420 * OFFSET * MINIFIER_VALUE,
						table_right_padding: 10 * OFFSET * MINIFIER_VALUE,
						table_row_spacing: 15 * OFFSET * MINIFIER_VALUE,
						table_top_padding: (TABLE_ROW_SPACING / 2) * MINIFIER_VALUE,
						table_column_spacing: TABLE_RIGHT_PADDING * MINIFIER_VALUE,
						table_row_divider_padding: 3 * OFFSET * MINIFIER_VALUE,
						minified_padding: MINIFIED_PADDING,
						minified_bottom_padding: MINIFIED_BOTTOM_PADDING,
				  }
				: null
		);
		let image = scaleDimensionsTo(await getImageDimensions(data), 210);

		pdf.addImage(data, 0, 0, image.width, image.height);
		pdf.save('');
	};

	const getImageDimensions = (base64) => {
		return new Promise((resolve, reject) => {
			let image = document.createElement('img');
			image.onload = function () {
				resolve({
					width: this.width,
					height: this.height,
				});
				image.remove();
			};
			image.src = base64;
		});
	};

	const getLatencyColor = () => {
		switch (true) {
			case state.latency <= 120:
				return '#68BC98';

			case state.latency <= 1100:
				return '#B27C1B';
			default:
				return '#E42627';
		}
	};

	return (
		<ThemeProvider theme={theme}>
			<div
				style={{
					display: 'none',
					position: 'fixed',
					overflow: 'hidden',
					// height: 'max-content',
					// width: `${(localStorage.getItem('js-pdf-page-width') || defaultPageWidth) * ratio}px`,
					// width: 'max-content',
					// right: '-100%',
					fontFamily: 'custom-arabic',
				}}
			>
				<canvas ref={canvas} width='400' height='150'></canvas>
				<a ref={download} download='.jpeg' />
			</div>

			<div
				id='latency'
				style={{
					position: 'fixed',
					bottom: '0',
					right: '0',
					paddingRight: '5px',
					paddingBottom: '5px',
					width: 'max-content',
					height: 'max-content',
					textAlign: 'right',
					zIndex: '5',
					fontSize: '16px',
					lineHeight: '1',
					color: getLatencyColor(),
				}}
			>
				{`${state.latency}ms`}
			</div>

			<div className='App' dir='rtl'>
				<div id='header'>
					<img
						src={visionLogo}
						id='vision-logo'
						width='240'
						height='60'
						style={{
							marginTop: '10px',
							marginRight: '10px',
						}}
					></img>
					<div
						style={{
							position: 'fixed',
							top: 0,
							left: 0,
							width: '100%',
							display: 'flex',
							justifyContent: 'center',
							alignItems: 'center',
						}}
					>
						<img
							src={maarifLogo}
							width='160'
							style={{
								marginTop: '12px',
							}}
						></img>
						{/* <img
							src={maarifLogo}
							width='160'
							style={{
								marginTop: '12px',
							}}
						></img> */}
					</div>

					<div
						id='noor-islam-logo'
						style={{
							flexDirection: 'row-reverse',
							justifyContent: 'center',
							alignItems: 'center',
							marginLeft: '10px',
						}}
					>
						<img src={noorIslamLogo} width='96' height='86'></img>
						<div
							style={{
								marginLeft: '10px',
								fontSize: '24px',
								fontWeight: 'bold',
								fontFamily: 'Arial',
								display: 'flex',
								flexDirection: 'column',
								justifyContent: 'center',
								alignItems: 'flex-end',
								height: '20px',
								lineHeight: '1',
								width: 'max-content',
							}}
						>
							<div>  </div>
							<div> </div>
						</div>
					</div>
				</div>
				<div id='main'>
					<div id='top'>
						<div
							id='main-title'
							style={{
								marginBottom: '18px',
								wordBreak: 'break-word',
								width: '100%',
								textAlign: 'center',
							}}
						>
							  
						</div>
						<div id='interactables'>
							<TextField
								ref={field}
								type='number'
								sx={{
									width: '100%',
									height: '62px',
									textAlign: 'right',
								}}
								InputLabelProps={{
									// shrink: true,
									sx: {
										fontSize: '20px',
									},
								}}
								InputProps={{
									sx: {
										fontSize: '20px',
										height: '62px',
									},
								}}
								id='main-field'
								label=' '
								value={state.field}
								onChange={onFieldChange}
								onBlur={onBlur}
								onKeyDown={(event) => {
									if (event.keyCode == 13) {
										onRequestSearch();
									}
								}}
							/>

							{/* <Button
								id='main-button'
								variant='contained'
								style={{
									height: '62px',
									width: '150px',
									fontSize: '25px',
								}}
								onClick={onRequestSearch}
							>
								
							</Button> */}

							<LoadingButton
								id='main-button'
								loading={state.loading}
								variant='contained'
								style={{
									height: '62px',
									fontSize: '25px',
								}}
								onClick={onRequestSearch}
							>
								
							</LoadingButton>
							{/* <button id='main-button'></button> */}

							{/* <input type='number' id='main-field' placeholder=' ' /> */}
						</div>

						<div
							id='error'
							style={{
								display: state.error ? 'block' : 'none',

								color: 'red',
								// whiteSpace: 'nowrap',
								// overflow: 'hidden',
								// textOverflow: 'ellipsis?',
								wordBreak: 'break-word',
								fontSize: '18px',
								marginTop: '3px',
							}}
						>
							{state.error}
						</div>

						{state.info && (
							<div
								id='info-area'
								style={{
									display: 'flex',
									flexDirection: 'column',
									marginTop: '10px',
								}}
							>
								<div
									style={{
										border: '1px solid #C4C4C4',
										borderRadius: '5px',
										fontSize: '18px',
										// paddingRight: '10px',
										marginBottom: '10px',
									}}
								>
									{Object.entries(state.info).map(([key, value], index) => {
										let maximum = Object.keys(state.info)
											.map((key) => measureText(switches[key], '18px custom-arabic'))
											.sort((a, b) => b - a)[0];

										let self = measureText(switches[key], '18px custom-arabic');

										return (
											<div
												key={`info-item-${index}`}
												style={{
													width: '100%',
												}}
											>
												<div
													style={{
														display: 'flex',
														flexDirection: 'row',
														width: '100%',
														justifyContent: 'flex-start',
														paddingRight: '5px',
														boxSizing: 'border-box',
													}}
												>
													<div
														style={{
															display: 'flex',
															alignItems: 'center',
															textAlign: 'center',
															wordBreak: 'break-word',
															width: '100%',
														}}
													>
														{switches[key]}
														<Divider
															style={{
																height: '100%',
																backgroundColor: '#C4C4C4',
																marginLeft: `${WEB_TABLE_COLUMN_SPACING}px`,
																marginRight: `${maximum - self + WEB_TABLE_COLUMN_SPACING}px`,
															}}
															orientation='vertical'
														/>
														<span style={{ fontFamily: 'Arial' }}>{value}</span>
													</div>
												</div>
												{index != Object.keys(state.info).length - 1 && <Divider style={{ width: '100%', backgroundColor: '#C4C4C4' }} />}
											</div>
										);
									})}
								</div>

								<div
									style={{
										width: '100%',
										display: 'flex',
										justifyContent: 'center',
										alignItems: 'center',
									}}
									id='interactable-buttons'
								>
									<Button variant='contained' onClick={closeInfo}>
										<div></div>
									</Button>
									<Button variant='contained' onClick={handleClick} ref={saveButton}>
										<div></div>
										<KeyboardArrowDownIcon />
									</Button>

									<Menu
										anchorEl={state.anchor}
										open={state.anchor != null}
										onClose={handleClose}
										sx={{
											marginTop: '2px',
										}}
										MenuListProps={{
											sx: {
												backgroundColor: '#1976D2',
												width: '140px',
												color: 'white',
											},
										}}
									>
										<MenuItem
											onClick={() => {
												saveAsDocument(true);
												handleClose();
											}}
										>
											PDF&nbsp;<span> </span>
										</MenuItem>
										<MenuItem
											onClick={() => {
												saveAsDocument();
												handleClose();
											}}
										>
											PDF&nbsp;<span></span>
										</MenuItem>
										<MenuItem
											onClick={() => {
												saveAsPhoto();
												handleClose();
											}}
										>
											JPG&nbsp;<span></span>
										</MenuItem>
									</Menu>
								</div>
							</div>
						)}
					</div>
				</div>
			</div>
		</ThemeProvider>
	);
}

export default App;
